<template>
  <div>
    <img :src="oneRoom.image" style="width:70%"/>
    <h4 @click="send" >{{oneRoom.title}}</h4>
    <p>{{oneRoom.price}}원</p>
    
  </div>  
</template>

<script>
export default {
  name : 'Card',
  props : {
    oneRoom : Object,
  },
  methods : {
    send(){
      this.$emit('openModal', this.oneRoom.id)
    }
  }   
}
</script>

