<template>
  <!--
    애니메이션은 if/else 같은 애들한테 적용함
    왜? 있어보이니까 총 세가지의 클래스를 이용함.
  
    name명-enter-from / name명-enter-active / name명-enter-to 들어올 때
    name명-leave-from / name명-leave-active / name명-leave-to 나갈 때
  -->
  <transition name="fade"> 
    <!-- 모달창 -->
    <Modal @closeBtn="modalOpen = false"
          :oneRoom="oneRoom"
          :modalCnt="modalCnt"
          :modalOpen="modalOpen" />
  </transition>
  <!--div class="start" :class="{ end : modalOpen }">
  <Modal @closeBtn="modalOpen = false"
         :oneRoom="oneRoom"
         :modalCnt="modalCnt"
         :modalOpen="modalOpen" />
  </div-->
  
  <!-- 메뉴바 -->
  <div class="menu">
    <a v-for="(abc, i) in menubar" v-bind:key="i">{{abc}}</a>
  </div>

  <!-- 할인 배너 -->
  <Discount v-if="showDiscount == true" />

  <button @click="priceSort">가격순 정렬</button>
  <button @click="priceSort2">가격역순 정렬</button>
  <button @click="nameSort">가나다순 정렬</button>
  <button @click="sortBack">되돌리기</button>
  
  <!-- 상품 리스트 -->
  <Card @openModal="modalOpen = true; modalCnt=$event" :oneRoom="oneRoom[j]" 
        v-for="(i,j) in oneRoom" :key="j"
         />

</template>
<script>
import data from './assets/port.js';
import Discount from './Discount.vue';
import Modal from './Modal.vue';
import Card from './Card.vue';

export default {
  name: 'App',
  data () {
    return {
      //object 자료 형태로 저장.
      modalOpen : false,
      price : ['50', '60', '70'],
      menubar : ['Home', 'Shop', 'About'],
      products : ['역삼동 원룸', '천호동원룸', '마포구원룸'],
      modalCnt : 0, //모달창번호 
      oneRoomOri : [...data],  //원본 데이터
      oneRoom : data,     //변형 데이터
      /*
        ,,,,같은 어레이를 사용하기 때문에 둘이 다른 객체가 아닌 같은 곳을 바라봄.
        그렇기 때문에~~~ [...array자료] 형식으로 작성해야 함.

        그런데!? 막 하다가 보면 작동 안 되는데,
        array는 등호로 집어넣으면 값을 넣는 게 아니라 공유하는 것.
        그렇기 때문에~~~아래에서도 똑같은 쉘?카피본 형식으로 써야 함.
      
      */
      showDiscount : true,

    }
  },
  methods : {
    increase(value){
        this.cnt[value] += 1;
    },
    priceSort(){
      /*
      sort() 함수란?
      var array = [3,4,2];
      array.sort(function(a,b){
        return a- b
      }); 이 있다고 하면
      a-b 값이 음수면 왼쪽 양수면 그대로. DESC 정렬임.ASC는 저기에 음수 곱하면 됨.
      
      sort()는 원본을 변형하는데,
      map()filter()는 원본을 보존해줌.
      */
      
      this.oneRoom.sort(function(a, b){
        return a.price-b.price
      });
    },
    priceSort2(){
      this.oneRoom.sort(function(a,b){
        return b.price - a.price
      });
    },
    nameSort(){
      this.oneRoom.sort(function(a,b){
        return a.title.charCodeAt(0) - b.title.charCodeAt(0);
      });
    },
    sortBack(){
      this.oneRoom = [...this.oneRoomOri];
    },
  },
  created(){

  },
  // mounted(){
  //   //라이프 사이클 훅 사용
  //   //마운트(HTML이 다 착장된 상태) 될 때 사용. 되기 전에는 beforeMount
  //   //여기서 this를 사용할 때, (funtion(){...}) 형식이 아니라 (()=>{...}_ 형식으로 써야 오류 안남.
  //   setTimeout(()=>{
  //     this.showDiscount = false;
  //   },2000);

  // },
  components: {
    Discount : Discount,
    Modal : Modal,
    Card : Card,
  }
}
</script>

