<!-- < + 엔터 자동 vue 파일 틀 생성. -->
<template>
  <div class="discount">
    <h4>지금 결제하면 {{persent}}% 할인</h4>
  </div>
</template>

<script>
export default {
    name : 'Discount',
    data (){
      return  {
        persent : 20,
      }
    },
    mounted(){
      setInterval(()=>{
        if(this.persent > 0){
          this.persent--;
        }    
      },1000);
    },
}
</script>

