<template>
  <div class="black-bg" v-if="modalOpen == true" >
    <div class="white-bg">
      <img :src="oneRoom[modalCnt].image" />
      <h4>{{oneRoom[modalCnt].title}}</h4>
      <p>{{oneRoom[modalCnt].content}}</p>
      <input v-model.number="month"/>
      <p>{{month}}개월 선택 -> {{oneRoom[modalCnt].price * month}}원</p>
      <button @click="close">닫기</button>
    </div>
  </div>
</template>

<script>
export default {
    name : 'Modal',
    data () {
      return {
        month : 1,
      }
    },
    watch : {
      month(a,b) {  
        if(a >= 13){
          alert("13개월 이상은 입력 불가능합니다.");
          this.month = b;
        }  
        else if(isNaN(a) == true) { //isNaN() 안에 숫자를 입력하면 false, 글자를 입력하면 true. 이거 말고도 typeof(a) == 'string' 가능.
          alert("문자는 입력 불가능합니다.");
          this.month = b;
        }          
      }
    },
    beforeUpdate(){
      if(this.modalOpen==true){
        if(this.month == 2){
          alert("3개월 이상만 가능합니다.");
          this.month = 1;
        }
      }
    },
    props : {
        oneRoom : Array,
        modalCnt: Number,
        modalOpen : Boolean,
    },
    methods :{
      close(){
        this.$emit('closeBtn')
      }
    }
}
</script>

